import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import ReactGA from "react-ga4";

const RouteChangeTracker = () => {
  const location = useLocation();
  const [initialized, setInitialized] = useState(false);

  // Sadece canl (production) ortamnda ve ilk render'da GA'y balat
  useEffect(() => {
    if (process.env.NODE_ENV === "production") {
      ReactGA.initialize("G-NRCHSQEMTF"); // Kendi ID'niz
      setInitialized(true);
    }
  }, []);

  // GA balatldktan sonra, her sayfa (location) deiiminde 'pageview' gnder
  useEffect(() => {
    if (initialized) {
      const pagePath = location.pathname + location.search;
      ReactGA.send({ hitType: "pageview", page: pagePath });

      // Tarayc konsolunda kontrol etmek iin bir log ekleyebilirsiniz.
      // console.log(`GA Pageview Sent: ${pagePath}`);
    }
  }, [initialized, location]);

  // Bu bileen ekranda hibir ey gstermez
  return null;
};

export default RouteChangeTracker;
